import { Component, Input } from '@angular/core';

@Component({
    selector: 'cmx-panel-card',
    template: require('./cmx-panel-card.component.html'),
    styles: [require('../../../../scss/cmx-panel-card/v1/cmx-panel-card.component.scss')]
})

export class CmxPanelCardComponent {
    @Input() public title: string;
    @Input() public masterHeight: string;
    @Input() public minHeight: string;
    @Input() public class: string;

    constructor() {
        if (this.masterHeight === undefined) {
            this.masterHeight = '220px';
        }
        if (this.minHeight === undefined) {
            this.minHeight = '0';
        }
        this.class = 'simple-card-border';
    }
}



